import { Navigate } from 'react-router-dom';
import { useAuth } from '../hooks';

/**
 * - Dac ruta este restricionat i utilizatorul este autentificat, afieaz <Navigate> ctre `redirectTo`.
 * - n caz contrar, afieaz componenta.
 */

export const RestrictedRoute = ({ component: Component, redirectTo = '/' }) => {
  // Se utilizeaz hook-ul useAuth pentru a verifica starea de autentificare a utilizatorului.
  const { isLoggedIn } = useAuth();

  // Dac utilizatorul este autentificat, redirecioneaz ctre ruta specificat ('/').
  // n caz contrar, afieaz componenta corespunztoare rutei actuale.
  return isLoggedIn ? <Navigate to={redirectTo} /> : Component;
};
