import { Navigate } from 'react-router-dom';
import { useAuth } from '../hooks';

/**
 * - Dac ruta este privat i utilizatorul este autentificat, randeaz componenta.
 * - Altfel randeaz <Navigate> pentru a redireciona ctre redirectTo.
 */

export const PrivateRoute = ({ component: Component, redirectTo = '/' }) => {
  // Se utilizeaz hook-ul `useAuth` pentru a obine starea autentificrii i pentru a actualiza informaiile (isRefreshing).
  const { isLoggedIn, isRefreshing } = useAuth();

  // Verific dac este necesar redirecionarea, adic dac utilizatorul nu este autentificat i nu se remprospteaz informaiile de autentificare.
  const shouldRedirect = !isLoggedIn && !isRefreshing;

  // Returneaz componenta dac NU este necesar redirecionarea, altfel randeaz un <Navigate> pentru a redireciona ctre ruta specificat ('/').
  return shouldRedirect ? <Navigate to={redirectTo} /> : Component;
};
